













import Vue from "vue";
import { store } from "@/store";
export default Vue.extend({
  name: "SpectrasOfMotif",
  props: ["motifid"],
  data() {
    return {
      columns: [
        {
          name: "name",
          label: "Name",
          sortable: true,
          field: "name"
        },
        {
          name: "prob",
          field: "prob",
          sortable: true,
          label: "Probability"
        },
        {
          name: "overlap",
          field: "overlap",
          sortable: true,
          label: "Overlap score"
        }
      ],
      pagination: {
        sortBy: "prob",
        descending: true,
        rowsPerPage: 25
      },
      selected: []
    };
  },
  computed: {
    data() {
      const overlaps = store.state.dataset.overlap_scores;
      return Object.entries(
        store.state.dataset.theta
      ).filter(([name, motifs]) => motifs.hasOwnProperty(this.motifid))
      .map(([name, motifs]) => {
          const overlap = overlaps[name][this.motifid];
          return {
            name,
            prob: motifs[(this as any).motifid],
            overlap
          }
      });
    }
  }
});
